body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f0f4f8;
  color: #333;
}

main {
  max-width: 1200px;
  margin: auto;
  padding: 1rem;
}

/* Container daftra catatan */
#notes-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}

/* Responsive */
@media (max-width: 600px) {
  main {
    padding: 0.5rem;
  }
}
